import * as React from 'react';
import Card from "./card"
import MiniView from "./miniView"
import "../../style/slider.scss"


const Slider = (props) => {
    const slider = React.useRef();

    let deltaW = 0



    React.useEffect(() => {
        slider.current.addEventListener("wheel", onMouseWheel);
        window.addEventListener("resize", (e) => {
            console.log(window.innerWidth)
        })
        return () => {
            slider.current.removeEventListener("wheel", onMouseWheel);
        }
    }, []);




    const onMouseWheel = (event) => {

        let rightLim = Math.ceil(slider.current.getBoundingClientRect().width - slider.current.parentNode.getBoundingClientRect().width);

        const delta = Math.sign(event.deltaY);
        deltaW = ((-delta * (rightLim / 5)) + +slider.current.style.marginLeft.slice(0, -2));
        console.log(rightLim)
        console.log(deltaW)
        if (deltaW > 0 || deltaW < -rightLim) {
            return null
        }
        else {
            requestAnimationFrame(() => {
                slider.current.style.marginLeft = `${deltaW}px`;
            })
        }
    }

    return (
        <div>
            <div className="slider-main">
                <div className="slider-body" ref={slider} style={{ transition: `all 1s ease-out`, marginLeft: "0" }}>
                    {props.cards.map((item, index) => (
                        <Card key={index} data={item} />
                    ))}
                </div>
            </div>
            <div className="flex-between slider-pages">
                <MiniView data={props.cards} />
                <div>scroll</div>
            </div>
        </div>
    );

}

export default Slider;