import React from "react"
import Slider from "../components/slider/slider"
import Layout from "../components/defaults/layout"
import SEO from "../components/defaults/seo"
import Cards from "../components/slider/portfolioCards"
import CvSection from "../components/works/cvSection"
import "../style/works/works.scss"

const worksdPage = () => (
    <Layout>
        <SEO title="Compercial Portfolios" />
        <div className="works-main">
            <CvSection />
            <Slider cards={Cards} />
        </div>
    </Layout>
)

export default worksdPage
